import React, { Component } from 'react'
import PropType from 'prop-types'
import ReactDom from 'react-dom'
import { creatstore } from 'redux'
import { Providers, connect } from 'react-redux'

import defaultstyle from '../public/css/default.css'

import commonstyle from '../public/css/common.css'

import indexstyle from '../public/css/index.css'

import Header from './components/header'

import Index from './components/index'

import { setFontSize, waterfall, imgIsload, Fetch } from './function/common'

import $ from 'n-zepto'


setFontSize()
class App extends Component {
	constructor() {
	    super();
	}

	componentDidMount(){
		
		var promise = imgIsload(".opt-img");		//获取拿到的 promise

		Promise.all( promise ).then(function(){		//待图片加载完成
			waterfall("waterbox", ".pin");

		})

		window.onresize = function(){
			setFontSize()
			waterfall("waterbox", ".pin");	
		}	

		
  	var opitions = {
  		method:"POST",
  		body:{
  			pageIndex:1,
  			pageLimit:6
  		}
	  }
		Fetch('http://bb.wxjysgcd.com/WuHu/Index/getVoteList',opitions)
			.then( (data) => {
				console.log(data)
			})

		var hei = $(window).height();
		// 创建iscroll之前必须要先设置父元素的高度，否则无法拖动iscroll
		$('#listloading, .listloadingClass').height(hei);	
		var Listloading = require('listloading');	
		var flg;	
		var m = 3;
		var n = 0;
		var listloading = new Listloading('#listloading', {
				    disableTime: true,  // 是否需要显示时间
				    pullUpAction : function(cb){   // 上拉加载更多
				       return false;
				        
				    },
				    pullDownAction : function(cb){  // 下拉刷新
				        // true则为默认加载 false为下拉刷新
				        if (flg) {
				            console.log('默认加载');
				        }
				        m = 3;
				       
				        //$('#order-list').html(__html);
				        // 执行完执行方法之后必须执行回调 回调的作用是通知默认加载已经全部执行完毕，程序需要去创建iscroll或者做下拉刷新动作
				        cb();
				    },
				    // iscroll的API 
				    iscrollOptions: {
				        //
				    }
				});	
		
	}

	render(){
		return(
			<div id="listloading">
				<div>
					<Header />
					<Index />
				</div>
			</div>
		)
	}
} 

ReactDom.render(
	<App />,
	document.getElementById("root")
)



